<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="goodsName"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div @click="sortTypeClick('all')" class="sort-item">综合</div>
      <div @click="sortTypeClick('sales')" class="sort-item">销量</div>
      <div @click="sortTypeClick('price')" class="sort-item">价格 </div>
    </div>

    <div v-if="goodsList.length>0" class="goods-list">
      <GoodsItem v-for="item in goodsList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getGoodsList } from '@/api/goods'
export default {
  name: 'SearchIndex',
  components: {
    GoodsItem
  },
  computed: {
    goodsName () {
      return this.$route.query.key || ''
    }
  },
  data () {
    return {
      goodsList: [],
      sortType: 'all',
      sortPrice: '0',
      categoryId: '0',
      page: 1
    }
  },
  created () {
    this.categoryId = this.$route.query.categoryId || '0'
    this.getList()
  },
  methods: {
    sortTypeClick (type) {
      this.sortType = type
      this.getList()
    },
    async getList () {
      const res = await getGoodsList(this.sortType, this.sortPrice, this.categoryId, this.goodsName, this.page)
      this.goodsList = res.data.list.data
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
